@import "sass/index";
@import "../../variables.module.scss";
$desktop-gap: 10px;
$mobile-gap: 5px;
.root {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  min-height: 301px;
  height: auto;
  visibility: hidden;

  overflow: hidden;
  background: $default;
  @include backgroundOptions;
}

.field {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: repeat(5, 1fr);
  gap: $desktop-gap;
  width: 100%;
  height: 100%;
  max-width: 287px;
  max-height: 283px;
}
.game {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  min-height: 90%;
  max-width: calc(var(--basketCount) * 30px);

  @media (max-width: 600px) {
    min-height: 80%;
  }

  & > canvas {
    display: flex;
    pointer-events: none;
  }

  & > .fps {
    position: absolute;
    left: 50px;
    top: 50px;
    color: #ffffff;
    z-index: 10px;
  }
}
.visible {
  visibility: visible;
}
