.root {
  display: grid;
  grid-template-rows: 15px;
  grid-template-columns: repeat(var(--basketCount), 26px);
  width: 100%;
  justify-content: space-between;
  gap: 3px;
  @media (max-width: 600px) {
    gap: 1px;
    grid-template-columns: repeat(var(--basketCount), minmax(16px, 21px));
  }
}
.basket {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Gilroy";

  font-size: 10px;
  line-height: 12px;
  font-weight: 700;

  letter-spacing: 0.549872px;

  color: #ffffff;

  border-radius: 2.11489px;
  background: #ad0aff;

  justify-self: center;
  width: 100%;
  height: 100%;
  @media (max-width: 600px) {
    font-size: 6.8734px;
    line-height: 9px;
  }
}
.win {
  border: 1px solid #ffffff;
  border-radius: 2.11489px;
}
